*{
    outline: none;
}

html{
    font-family: 'Roboto Condensed', sans-serif !important;
    font-size: 10px !important;
}

body{
    height: 100vh;
}

#calendar{
    width: 50%;
    height: 50%;
    margin: 0px auto;
    border-radius: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

#calendar .controls{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    height: 10%;
    width: 30%;
    border-bottom: 1px solid;
    -ms-flex-pack: center;
        justify-content: center;
    position: relative;
    margin-bottom: 1rem;
}

.controls .month, .controls .year{
    z-index: 111;
    position: relative;
    font-size: 1.6rem;
}

.controls .info{
    width: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
}

.controls .btns{
    position: absolute;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.btns button{
    border: none;
    cursor: pointer;
}

.red{
    color: #f00;
}

.days{
    width: 100%;
    display: -ms-flexbox;
    display: flex;
}

.days table{
    border: 1px solid;
    width: 100%;
    height: 100%;
}

.days table tr{
    border: 1px solid;
    display: -ms-flexbox;
    display: flex;
}

.days table td, .days table th{
    border: 1px solid;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    font-size: 1.6rem;
    width: 15%;
    min-height: 5rem;
}

.days table .currDay{
    color: #00f; 
    font-weight: bold;
}

.days table .currDay:hover{
    color: #000;
}

.days table td:hover{
    cursor: pointer;
    color: #fff;
    background: #b7c48c !important;
}


