.Navigation{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    height: 100vh;
}

.Navigation nav{
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100%;
    height: 5rem;
}

.Navigation ul{
    display: -ms-flexbox;
    display: flex;
    width: 80%;
    -ms-flex-pack: justify;
        justify-content: space-between;
    height: 100%;
    -ms-flex-align: center;
        align-items: center;
}

.Navigation ul li{
    font-size: 2rem;
    font-weight: bold;
    background: #1490c1;
    color: #fff;
    height: 5rem;
    width: 20rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    cursor: pointer;
}

.Navigation ul li:hover{
    background: #21a4d8;
}


